.form {
  // min-width: 570px;
  max-width: 100%;
  overflow: hidden;

  @include vp-1023 {
    // max-width: 670px;
    // width: 100%;
    margin-left: 0;
  }

  @include vp-767 {
    // min-width: 270px;
    display: flex;
    flex-direction: column;
  }
}

.form__required-data {
  // background-color: red;
  // display: contents;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1vw;
  margin: 0;
  margin-bottom: 1vw;
  border: none;
  // width: 100%;
  padding: 0;

  @include vp-1023 {
    // grid-template-columns: repeat(1, auto);
    width: 95%;
    height: 50px;
    gap: 30px;
    margin-bottom: 35px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 20px;
    margin-bottom: 90px;
  }

  // &:first-child {
  //   grid-area: name-form;
  // }

  // &:last-child {
  //   grid-area: phone-form;
  // }
}

.form input {
  padding: 10px;
  font-family: $ff-ptmono;
  font-weight: 400;
  font-size: 16px;
  line-height: 21.654px;
  background-color: rgba($color-black-perl, 0.15);
  border: none;
  color: $color-default-white;

  &::placeholder {
    color: $color-default-white;
  }

  @include vp-1023 {
    padding: 20px;
  }

  @include vp-767 {
    padding: 15px;
  }
}

.form textarea {
  min-height: 155px;
  display: block;
  width: 100%;
  margin: 0;
  margin-bottom: 34px;

  font-family: $ff-ptmono;
  font-weight: 400;
  font-size: 16px;
  line-height: 21.654px;
  background-color: rgba($color-black-perl, 0.15);
  border: none;
  color: $color-default-white;
  padding: 10px;
  padding-top: 25px;

  &::placeholder {
    color: $color-default-white;
  }

  @include vp-1023 {
    padding-top: 30px;
    width: 95%;
    height: 190px;
    padding: 45px;
    padding-left: 20px;
  }

  @include vp-767 {
    // min-height: 180px;
    height: 180px;
    margin-bottom: 25px;
    padding: 15px;
  }
}

.form__submit-wrapper {
  border: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;

  align-items: flex-end;
  gap: 1.7vw;
  width: 100%;

  @include vp-1023 {
    align-items: center;
    gap: 0.1vw;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    align-items: center;
    gap: 25px;
  }

  &--modal {
    flex-direction: column;
    align-items: stretch;
  }
}

.form__button {
  width: 14vw;
  min-height: 0.1vw;
  padding: 15px 25px;

  color: $color-default-white;
  font-size: 16px;
  letter-spacing: 0.8px;
  border: none;
  cursor: pointer;

  @include vp-1023 {
    margin-top: 20px;
    width: 34vw;
  }

  @include vp-767 {
    width: 95%;
  }

  &--modal {
    width: 100%;
  }
}

.form__submit-wrapper label {
  display: flex;
  align-items: center;
  color: $color-default-white;
  font-family: $ff-ptmono;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal; //16
  cursor: pointer;

  @include vp-1023 {
    max-width: 235px;
    margin-top: 15px;
    margin-left: 0;///////////////////////
  }

  @include vp-767 {
    min-width: 95%;
  }
}

.form__check-mark {
  position: relative;
  width: 15px;
  height: 15px;
  margin-right: 12px;
  flex-shrink: 0;
  border: 1px solid $color-default-white;
  border-radius: 2px;

  &::before {
    content: "";
    display: none;
    position: absolute;
    left: 10%;
    top: 50%;
    width: 15px;
    height: 15px;
    // width: 9px;
    // height: 6px;
    transform: translateY(-50%);
    background: url("../../img/svg/agree.svg") center no-repeat;
  }
////////
  @include vp-767 {
    height: 14px;
  }//////
}

.form__submit-wrapper input {
  position: absolute;
  //   width: 14px;
//   height: 14px;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  // white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);

  &:checked + .form__check-mark {
    &::before {
      display: block;
    }
  }
}

.form--modal {
  // width: 100%;
  min-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  // background-color: ;
  // margin: 0;

  & input {
    margin-bottom: 20px;
  }

  & button {
    margin-bottom: 20px;
  }

  // & textarea {
  //   margin-bottom: 5px;
  // }

  // & .form__checkbox {
  //   margin-left: 9px;
  // }
}

